.mobile-nav {
    align-items: center;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    left: -100%;
    position: fixed;
    top: 0;
    transition: all .5s ease-in-out;
    width: 100%;
    z-index: 999999;
}

.open-nav {
    left: 0;
}

.mobile-nav__links {
    display: flex;
    flex-direction: column;
    font-size: 30px;
    gap: 40px;
    list-style: none;
    text-align: center;
    font-weight: 500;
}

.mobile-nav__links a {
    font-size: 30px;
}

.mobile-nav__links a:hover {
    color: var(--blue);
}

.mobile-nav__close {
    cursor: pointer;
    font-size: 3rem;
    position: absolute;
    right: 3.5rem;
    top: 3.5rem;
    transition: all .3s;
}

.mobile-nav__XBtn {
    font-weight: 900;
    -webkit-font-smoothing: antialiased;
    display: var(--fa-display,inline-block);
    font-style: normal;
    font-variant: normal;
    line-height: 1;
    text-rendering: auto;
}

.mobile-nav__close:hover {
    color: var(--blue);
}

.mobile-nav__hamburger {
    cursor: pointer;
    display: none;
    font-size: 2.8rem;
    transition: all .3s;
}

.mobile-nav__hamburgerIcon:hover {
    color: var(--blue);
}

@media (max-width: 950px) {

    .mobile-nav__links {
        font-size: 40px;
        gap: 20px;
    }
    .mobile-nav__XBtn {
        font-size: 30px;
    }
    .mobile-nav__hamburger {
        display: flex;
    }
}

nav {
    width: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); 
    background-color: #fff;
}

.mobile__icons {
    display: flex;
    gap: 20px;
    margin-top: 20px;
}

.mobile-nav__icon:hover {
    color: var(--blue2);
}

.nav {
    align-items: center;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    width: 90%;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
}

.nav__logo {
    font-size: 30px;
    font-weight: 900;
    color: #2d2e32;
}

.nav__links {
    align-items: center;
    display: flex;
    font-family: Rubik,sans-serif;
    font-size: 18px;
    font-weight: 700;
    gap: 20px;
}

.nav__link {
    color: #555;
    cursor: pointer;
    transition: all .3s;
}

.nav__link:hover {
    color: var(--blue);
}

.nav__btn {
    background-color: var(--blue2);
    border-radius: 3px;
    color: #fff;
    padding: 15px 30px;
    transition: all .3s;
}

@media (max-width: 950px) {
    .nav__btns {
        display: none;
    }
    .nav__links {
        display: none;
    }
}